@font-face {
  font-family: "trevor-pi";
  src: url("/wp-content/plugins/trevor/static/icon-font/trevor-pi_0abab36189db23f6df72632470f86327.eot");
  src: url("/wp-content/plugins/trevor/static/icon-font/trevor-pi_0abab36189db23f6df72632470f86327.eot?#iefix") format("embedded-opentype"),
       url("/wp-content/plugins/trevor/static/icon-font/trevor-pi_0abab36189db23f6df72632470f86327.woff2") format("woff2"),
       url("/wp-content/plugins/trevor/static/icon-font/trevor-pi_0abab36189db23f6df72632470f86327.woff") format("woff"),
       url("/wp-content/plugins/trevor/static/icon-font/trevor-pi_0abab36189db23f6df72632470f86327.ttf") format("truetype"),
       url("/wp-content/plugins/trevor/static/icon-font/trevor-pi_0abab36189db23f6df72632470f86327.svg#trevor-pi") format("svg");
  font-weight: normal;
  font-style: normal;
}

@media screen and (-webkit-min-device-pixel-ratio:0) {
  @font-face {
    font-family: "trevor-pi";
    src: url("/wp-content/plugins/trevor/static/icon-font/trevor-pi_0abab36189db23f6df72632470f86327.svg#trevor-pi") format("svg");
  }
}

[data-icon]:before { content: attr(data-icon); }

[data-icon]:before,
.trevor-pi-twitter-brands:before {
  display: inline-block;
  font-family: "trevor-pi";
  font-style: normal;
  font-weight: normal;
  font-variant: normal;
  line-height: 1;
  text-decoration: inherit;
  text-rendering: optimizeLegibility;
  text-transform: none;
  -moz-osx-font-smoothing: grayscale;
  -webkit-font-smoothing: antialiased;
  font-smoothing: antialiased;
}

.trevor-pi-twitter-brands:before { content: "\f100"; }

$font-trevor-pi-twitter-brands: "\f100";
